}

.rot1 {
  animation: skew 5s alternate;
  transform: skew(0deg);
    animation-direction: normal;
}


.rotate-s-loader {
  animation: rotate-s-loader 5s alternate;
}
@keyframes rotate-s-loader {
  from {
    transform: rotate(20deg);
  }
  to {
    transform: rotate(0);
  }
}

@keyframes skew {
  0% {
    transform: skew(-10deg);
  }
  100% {
    transform: skew(0deg);
  }
}

